/* GLOBAL */
html {
  font-size: 100% !important; }

body {
  line-height: calc(1.4 * 1.4rem) !important;
  font-size: 1.4rem !important;
  font-weight: 400 !important; }

strong, b {
  font-style: normal;
  font-weight: 700; }

h1, h2 {
  text-transform: uppercase; }

.post-content h2 {
  letter-spacing: -0.03em !important;
  font-size: 1.4rem !important; }

/* HEADER */
.fusion-header {
  border-bottom: #0A5B86 4px solid; }

/* MENU */
.fusion-main-menu > ul > li > a {
  text-transform: uppercase;
  font-weight: 600 !important; }

/* HOMEPAGE */
.tfs-slider .slide-content-container .slide-content {
  padding: 2.3rem 2.8rem 2.5rem 2.8rem;
  background-color: #0A5B86;
  margin-right: 0;
  max-width: 660px !important; }
  .tfs-slider .slide-content-container .slide-content .heading {
    margin-bottom: 1.02em; }
  .tfs-slider .slide-content-container .slide-content .caption {
    margin-bottom: 1.0em; }
  .tfs-slider .slide-content-container .slide-content h1 {
    font-family: "soleil";
    font-weight: 600;
    font-size: 1.9em !important;
    line-height: 120% !important;
    text-transform: none; }
  .tfs-slider .slide-content-container .slide-content h2 {
    color: #A2CAF0; }
  .tfs-slider .slide-content-container .slide-content .fusion-button.btn-homepage {
    background-color: #fff;
    border-radius: 0;
    padding-left: 3.6em;
    padding-right: 3.6em; }
    .tfs-slider .slide-content-container .slide-content .fusion-button.btn-homepage .fusion-button-text {
      color: #C17000;
      font-size: 1.6em;
      font-family: "soleil";
      font-weight: 700; }
  .tfs-slider .slide-content-container .slide-content .btn-homepage:hover .fusion-button-text {
    color: #8B5100; }

.tfs-slider .slide-content-container .tfs-button-1 {
  margin-top: 1.5em;
  margin-bottom: 0.9em; }

/* STUDY TEAM */
.study-team-container {
  padding-bottom: 4.3rem;
  margin-bottom: 2.3em;
  border-bottom: #707070 1px solid; }
  .study-team-container .study-team-text-container {
    margin-left: 0.5em; }
  .study-team-container .study-team-img {
    margin: 0; }

.study-team-container:last-child {
  border: none; }

/* PAGES */
.fusion-page-title-bar .landing-page-title-bar {
  background-position: 100% 0 !important;
  background-size: auto;
  background-repeat: no-repeat; }

.fusion-page-title-bar .content-margin,
.fusion-page-title-bar .landing-page-title-bar {
  height: 100%; }
  .fusion-page-title-bar .content-margin .fusion-page-title-wrapper,
  .fusion-page-title-bar .landing-page-title-bar .fusion-page-title-wrapper {
    align-items: flex-end; }
    .fusion-page-title-bar .content-margin .fusion-page-title-wrapper h1.entry-title,
    .fusion-page-title-bar .landing-page-title-bar .fusion-page-title-wrapper h1.entry-title {
      font-weight: 700;
      letter-spacing: -0.03em; }

.content-margin {
  margin-left: 18.2rem;
  margin-right: 19.2rem; }

/* FOOTER */
.fusion-footer-widget-area .footer-menu-and-legal {
  text-align: center;
  color: #fff; }
  .fusion-footer-widget-area .footer-menu-and-legal a {
    text-decoration: underline;
    color: #fff;
    font-weight: 400;
    font-size: 0.9em; }
  .fusion-footer-widget-area .footer-menu-and-legal a:hover {
    color: #ccc; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-menu {
    display: inline-block; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-menu ul {
    list-style: none; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-menu li {
    float: left;
    padding: 0 10px;
    border-right: #fff 1px solid; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-menu li:last-child {
    border-right: none;
    padding-right: 0; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-menu a {
    font-size: 1.4em; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-legal {
    font-size: 0.8em; }
  .fusion-footer-widget-area .footer-menu-and-legal .footer-legal a {
    font-size: 1.0em; }
